*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%;
  font-family: 'cursive';
  font-weight: bold; }

button {
  cursor: pointer; }

.w-50 {
  display: inline; }

.input-font {
  font-size: 2.25rem; }
  @media screen and (max-width: 600px) {
    .input-font {
      font-size: 1.2rem; } }

@keyframes movefromLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem); }
  80% {
    transform: translateX(1rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes movefromRight {
  0% {
    opacity: 0;
    transform: translateX(10rem); }
  80% {
    transform: translateX(-1rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes movefromBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

.u-center-text {
  text-align: center;
  margin: 0 auto; }

.u-mrg-big {
  margin-bottom: 8rem; }

.u-mrg-small {
  margin-bottom: 2rem; }

.u-mrg-medium {
  margin-bottom: 4rem; }

.no-mrg {
  margin: 0; }

.btm-right {
  position: absolute;
  bottom: 0rem;
  right: 0rem; }

.tp-right {
  display: flex;
  justify-content: flex-end; }

.potter__self {
  height: 120vh;
  font-size: 1.3rem; }
  .potter__self--House {
    font-size: 3rem;
    font-weight: bold;
    margin-top: 7rem; }
    @media screen and (max-width: 600px) {
      .potter__self--House {
        font-size: 1.6rem; } }
  .potter__self--know {
    position: absolute;
    top: -4rem;
    right: 2.5rem;
    font-size: 1.1rem; }
    @media screen and (max-width: 600px) {
      .potter__self--know {
        font-size: 0.64rem; } }

.potter__list {
  overflow-y: scroll;
  height: 95vh;
  color: black;
  font-size: 1.7rem; }

body {
  background-image: url("./Background.jpg");
  height: 120vh;
  background-repeat: no-repeat;
  background-size: cover; }

#root {
  background-color: rgba(255, 255, 255, 0.4); }
  @media screen and (max-width: 600px) {
    #root {
      height: 120vh; } }

.Search__button {
  margin-top: 3rem; }
  .Search__button--1 {
    position: absolute;
    top: 3rem;
    right: 0;
    font-size: 3rem; }
  .Search__button--2 {
    position: absolute;
    top: 5rem;
    font-size: 3rem; }

.Tilt {
  width: 12.5rem;
  height: 12.5rem; }
  @media screen and (max-width: 600px) {
    .Tilt {
      width: 7rem;
      height: 7rem; } }
  .Tilt-inner {
    padding-top: 5px; }
  @media screen and (max-width: 600px) {
    .Tilt__image {
      height: 5rem; } }

.Logo {
  width: 17rem; }
  @media screen and (max-width: 600px) {
    .Logo {
      width: 11rem; } }

@media screen and (max-width: 600px) {
  .signin {
    height: 17rem; } }

.signin_info {
  width: 60%; }

.row {
  width: 128rem;
  margin: 0px auto;
  margin-bottom: 3rem; }
  .row::after {
    content: "";
    display: table;
    clear: both; }
  .row [class^="col-"] {
    float: left;
    margin-top: 3rem; }
    .row [class^="col-"]:not(:last-child) {
      margin-right: 2rem; }
  .row .col-1-of-2 {
    width: calc(( 128rem - 2rem)/2); }
  .row .col-1-of-3 {
    width: calc((128rem - 2* 2rem)/3); }
  .row .col-1-of-4 {
    width: calc((128rem - 3*2rem)/4); }
  .row .col-2-of-3 {
    width: calc((2*(128rem - 2*2rem)/3 ) + 2rem); }
  .row .col-2-of-4 {
    width: calc(2 *((128rem - 3*2rem)/4) + 2rem); }
  .row .col-3-of-4 {
    width: calc(3 *((128rem - 3*2rem)/4) + 2 * 2rem); }

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1; }
